import {useDispatch} from "react-redux";

export default function Icons(props){
    const dispatch = useDispatch();

    if(props.image){
        return <div  className="w-full flex overflow-auto bg-white z-10 relative">
            {props.image.split(",").map((el, i) =><div key={i + "image-product"}  onClick={() => dispatch({type:"COUNTIMAGE",preload:i})} className="m-2 w-[100px] p-2 border flex-none "><img className="w-full cursor-pointer" src={el}/></div>)}
        </div>
    }else {
        return <div></div>
    }

}