import {useEffect, useState} from "react";
import {useParams} from "react-router-dom";
import {useSelector} from "react-redux";
import ProductItem from "./ProductItem";


export default function Product(props){
    const [product, setProduct] = useState([{}])
    const selectProduct = useSelector((store)=>store.getPatchProduct);
   let params = useParams();
    useEffect(()=>{
        setProduct(selectProduct.product?selectProduct.product:[{}])
    },[selectProduct.product])
    return <div>{product.filter((f)=>f.alias === params.products).map((el)=><div key={el.id + "product"}><ProductItem params = {params} showModalTopRight={props.showModalTopRight} setShowModalTopRight={props.setShowModalTopRight} el = {el} /></div>)}</div>
}